.skeleton {
  position: relative;
  overflow: hidden;
}

.skeleton:before {
  will-change: transform;
  width: 100%;
  height: 100%;
  content: "";
  z-index: 100;
  background: linear-gradient(120deg, #fff0 30%, hsla(0, 0%, 100%, var(--skeleton-flash-color)) 50%, #fff0 70%);
  animation: 1s infinite skeleton-ani;
  position: absolute;
  top: 0;
}

.skeleton:after {
  content: "";
  z-index: 10;
  width: 100%;
  height: 100%;
  background: var(--skeleton-bg-color);
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes skeleton-ani {
  0% {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(100%);
  }
}

@keyframes bounce {
  0% {
    transform: scale(1)translateY(0);
  }

  10% {
    transform: scale(1.1, .9)translateY(0);
  }

  30% {
    transform: scale(.9, 1.1)translateY(-20%);
  }

  50% {
    transform: scale(1.05, .95)translateY(0);
  }

  57% {
    transform: scale(1)translateY(-7%);
  }

  64% {
    transform: scale(1)translateY(0);
  }

  100% {
    transform: scale(1)translateY(0);
  }
}

